<script>
export default {
  data() {
    return {
      newStudent: {
        first_name: '',
        name: '',
        age: '',
      },
    };
  },
  props: ['student'],
  methods: {
    addStudent() {
      this.$emit('add-student', this.newStudent);
    },
  },
};
</script>

<template lang="pug">
div
  input(v-model="newStudent.first_name", placeholder="first nems")
  input(v-model="newStudent.name", placeholder="NAME")
  input(v-model="newStudent.age", placeholder="AGE")
  button(@click="addStudent") Ajouter
</template>

<style>
</style>



