<script>
  export default {
    props: ['student'],
    methods: {
      remove() {
        this.$emit('remove');
      },
    },
  };
</script>

<template lang="pug">
  div
    | {{student.first_name}} {{student.name}} {{ student.age }}
    button(@click="remove") Supprimer
</template>

<style>
</style>



